.main {
  font-family: Roboto;
  font-size: 16px;
  padding-top: 30px;
  padding-bottom: 60px;
  background: #E5E5E5;
}

.main__slider {
  position: relative;
  width: 100%;
  height: 360px;
  overflow: hidden;
}

.main__slider-dots {
  position: absolute;
  bottom: 10px;
  display: flex;
  width: 100%;
  justify-content: center;
}

.main__slider-dots li {
  cursor: pointer;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin-right: 15px;
  background: #FFF;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.15);
}

.main__slider-dots li button {
  cursor: pointer;
  background: transparent;
  color: transparent;
}

.main__slider-dots li button:before {
  color: #FFF;
}

.main__slider-dots li.slick-active {
  background: #03996C;
}

.main__slider-dots li.slick-active button:before {
  color: #03996C;
}

.main__slider-item {
  width: 100%;
  height: 360px;
  overflow: hidden;
}

.main__slider-image {
  width: 100%;
  height: auto;
}

.main__section {
  padding-top: 15px;
}

.main__section-header {
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
}

.main__section-title {
  font-size: 18px;
  color: #232323;
}

.main__section-link {
  color: #326BB5;
}

.main__section-link:hover {
  text-decoration: underline;
}

.main__section-link-arrow {
  vertical-align: middle;
  margin-left: 10px;
}

.main__seo {
  position: relative;
  padding-bottom: 15px;
  width: 100%;
  font-size: 16px;
  font-family: Roboto;
}

.main__seo-content {
  height: 96px;
  color: #999;
  overflow: hidden;
  transition: max-height .5s ease-out;
}

.main__seo-content:before,
.main__seo-content:after {
  content: "";
  display: table;
}

.main__seo-content:after {
  clear: both;
}

.main__seo-content h1,
.main__seo-content h2,
.main__seo-content h3,
.main__seo-content h4 {
  line-height: 20px;
  padding: 10px 0;
  color: #000;
}

.main__seo-content p {
  text-indent: 16px;
  text-align: justify;
  padding-bottom: 5px;
  line-height: 18px;
}

.main__seo-content ul {
  list-style: disc inside;
}

.main__seo-content ul li {
  padding-bottom: 5px;
}

.main__seo-content img {
  width: auto;
  height: auto;
}

.main__seo-read-more {
  position: absolute;
  font-size: 16px;
  right: 0;
  color: #326BB5;
  cursor: pointer;
  bottom: -5px;
}

.main__seo-read-more:hover {
  color: #1B9FCF;
}

.catalog-list {
  font-family: Roboto;
  font-size: 14px;
  color: #000;
  margin: 0 -10px;
}

.catalog-list:before,
.catalog-list:after {
  content: "";
  display: table;
}

.catalog-list:after {
  clear: both;
}

.catalog-list__item {
  background: #FFFFFF;
  box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  height: 450px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 25px 20px 15px;
  transition: all 0.3s .1s;
}

.catalog-list__item:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  box-shadow: 0 6px 10px 1px rgba(0, 0, 0, 0.48);
}

.catalog-list__item:hover .catalog-list__title-sub {
  color: #2dbe50;
}

.catalog-list > [class*="cell-"] {
  padding: 0 10px;
  margin-bottom: 20px;
}

.catalog-list__scale {
  overflow: hidden;
}

.catalog-list__scale > * {
  transition: -webkit-transform .3s ease .2s;
  transition: transform .3s ease .2s;
  transition: transform .3s ease .2s, -webkit-transform .3s ease .2s;
}

.catalog-list__scale:hover > * {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.catalog-list__img {
  width: 100%;
  height: 220px;
}

.catalog-list__title {
  font-size: 16px;
  text-align: center;
  padding-top: 15px;
  height: 55px;
  overflow: hidden;
}

.catalog-list__title-sub {
  padding-top: 10px;
  text-align: center;
  color: #999999;
  transition: .3s .2s;
}

.catalog-list__link:hover {
  text-decoration: underline;
  color: #2dbe50;
}

.catalog-list__price {
  text-align: center;
  padding-top: 10px;
  padding-bottom: 15px;
}

.catalog-list__price-value {
  font-weight: bold;
  font-size: 20px;
  color: #03996C;
}

.catalog-list__controls {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.catalog-list__rating {
  color: #999999;
}

.catalog-list__rating-icon {
  vertical-align: bottom;
}

.catalog-list__favorite-icon {
  cursor: pointer;
  transition: .3s;
}

.catalog-list__favorite-icon:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.catalog-list__favorite-icon:hover > path {
  fill: #F6B639;
}

