.paginator {
  box-sizing: border-box;
  height: 40px;
}

.paginator__list {
  display: flex;
  margin: 0 auto;
  box-sizing: border-box;
  justify-content: space-between;
  padding: 0 45px;
  background: #fff;
  box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  max-width: 600px;
  height: 100%;
}

.paginator__list.hidden {
  display: none;
}

.paginator__item {
  margin-right: 10px;
}

.paginator__item--angle-l {
  margin-right: 10px;
}

.paginator__item--angle-r {
  margin-left: -5px;
}

.paginator__item:last-child {
  margin-right: 0;
}

.paginator__link {
  font-family: Roboto;
  line-height: 40px;
  padding: 0 10px;
  font-size: 14px;
  display: block;
  color: #000;
}

.paginator__link--active {
  color: #03996C;
  font-weight: bold;
}

.paginator__link--active.hidden {
  display: none;
}

.paginator__angle {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  padding: 0 10px;
}

.header {
  font-family: Roboto;
  font-size: 14px;
  color: #000;
  background: #FFF;
}

.header__first-line {
  padding: 10px 0;
}

.header__second-line {
  height: 40px;
}

.header__items-wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  height: 40px;
}

.header__lang {
  padding-right: 40px;
}

@media screen and (max-width: 1023px) {
  .header__lang {
    display: none;
  }
}

.header__phones {
  height: 40px;
}

@media screen and (max-width: 1023px) {
  .header__phones {
    flex-direction: column;
    align-items: flex-start;
  }
}

.header__add-product {
  max-width: 200px;
  width: 100%;
}

.header__icon {
  position: relative;
}

.header__icon-counter {
  position: absolute;
  bottom: calc(100% - 9px);
  left: calc(100% - 9px);
  font-weight: 900;
  font-size: 10px;
  text-transform: uppercase;
  color: #FFFFFF;
  background: #03996C;
  width: 17px;
  height: 17px;
  text-align: center;
  border-radius: 100%;
  line-height: 17px;
  display: none;
}

.header__icon-counter.active {
  display: block;
}

.header__icon-title {
  padding-left: 10px;
}

.header__category {
  max-width: 270px;
  width: 100%;
  height: 40px;
  position: relative;
  z-index: 101;
}

.header__search {
  display: flex;
  flex-grow: 1;
  flex-flow: row nowrap;
  height: 40px;
}

.header__search-input {
  display: block;
  background: #FFFFFF;
  border: 1px solid #C4C4C4;
  box-sizing: border-box;
  flex-grow: 1;
  max-width: 420px;
  height: 100%;
  padding: 10px 20px;
  text-overflow: clip;
  transition: all 0.3s;
}

.header__search-input:focus {
  border-color: #21c997;
}

.header__search-select {
  position: relative;
  width: 100%;
  max-width: 305px;
}

.header__search-dropdown {
  border-left: none;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.05);
  max-height: 425px;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 101;
  padding: 10px 0;
}

.header__search-dropdown-item {
  padding: 6.5px 20px;
  cursor: pointer;
}

.header__search-dropdown-item--area {
  font-weight: bold;
}

.header__search-dropdown-item:hover {
  background: #e6e6e6;
}

.header__search-submit {
  max-width: 145px;
  width: 100%;
}

.footer {
  background: #03996C;
  padding: 40px 0 80px;
  font-size: 14px;
  font-family: Roboto;
  color: #FFFFFF;
}

.footer__list-item {
  padding-bottom: 15px;
}

.footer__list-link:hover {
  color: #fff;
  text-decoration: underline;
}

.footer__list-title {
  font-weight: bold;
  font-size: 16px;
}

.footer__list-text {
  color: #fff;
}

.footer__social {
  display: flex;
  flex-flow: row wrap;
}

.footer__social-item {
  margin-right: 15px;
}

.footer__social-item:last-child {
  margin-right: 0;
}

.footer__rights {
  display: block;
  text-align: center;
}

.button {
  font-family: Roboto;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  text-transform: uppercase;
  background: #F6B639;
  border-radius: 2px;
  padding: 10px 30px;
  text-align: center;
  color: #fff;
  width: 100%;
  transition: all .3s ease-in;
}

.button:hover {
  background-color: #f8c460;
}

.button--search {
  background: #03996C;
  border-radius: 0;
}

.button--search:hover {
  background-color: #04c188;
}

.button--category {
  height: 100%;
  background: #03996C;
  position: relative;
  border-radius: 0;
}

.button--category:hover {
  background-color: #04c188;
}

.button--more {
  background-color: #FF9C28;
}

.button--more:hover {
  background-color: #ffaf51;
}

.button__icon {
  margin: 0 15px;
  display: block;
}

.category-menu {
  padding-top: 10px;
  position: absolute;
  width: 100%;
  left: 0;
  top: 100%;
  background: #fff;
  z-index: 101;
  font-size: 14px;
  font-family: Roboto;
  color: #000000;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.12);
  display: none;
}

.category-menu__item:hover {
  background: #e6e6e6;
}

.category-menu__item-icon {
  vertical-align: text-bottom;
  margin-right: 10px;
}

.category-menu__item-link {
  padding: 5px 10px 10px;
  display: block;
}

